.header {
  // position: fixed;
  position: relative;
  z-index: 100;
  // left: 0;
  // right: 0;
  // top: 0;

  &__logo {
    position: relative;
    z-index: 20;

    // @include  mobile{
    //   max-width: 120px;
    //   max-height: 55px;
    //   height: 100%;
    // }
  }

  &__inner {
    padding: 19px clamp(20px, 4.167vw, 60px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: padding .3s;
    background-color: rgba(var(--bg-c), 0.95);
    box-shadow: 0 6px 25px 0 rgba(0, 0, 0, 0.15);
    border-radius: 20px;
  }

  &__menu {    
    @include for-tablet {
      display: contents;

      .main-nav {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
      }
    }

    @include tablet {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 50px;
      position: fixed;
      inset: 0;
      z-index: 10;
      opacity: 0;
      transition: transform .4s ease-out , opacity .4s ease-out;

      .main-nav__list {
				align-items: flex-start;
				flex-direction: column;
        align-items: center;
        gap: 25px;

        a {
          @include tablet {
            font-size: 20px;
          }
        }
      }
    }

    &.active {
			opacity: 1;
			transform: translateX(0%);
		}
  }

  // &.bg {
  //   background-color: rgba(var(--bg-e),1.0);

  //   .header__inner {
  //     padding: Max(15px, 15rem) 0;
  //   }
  // }


  &__btn {
    height: 43px;
    max-width: Max(146px, 146rem);
}
}